* {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  box-sizing: border-box;
}

.top_banner {
  padding: 30px 40px;
  width: 100%;
  background: #e3edf6;
}

.top_banner .container {
  max-width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.top_banner .container .detail h2 {
  font-size: 42px;
  color: #010f1c;
  margin-right: 50%;
  margin-bottom: 50px;
}

.link-home {
  margin-top: 20px;
  display: flex;
  width: 135px;
  justify-content: space-between;
  padding: 10px 20px;
  text-decoration: none;
  border-radius: 5px;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  -ms-border-radius: 5px;
  -o-border-radius: 5px;
  color: #010f1c;
  background: #fff;
  transition: 0.5s;
  -webkit-transition: 0.5s;
  -moz-transition: 0.5s;
  -ms-transition: 0.5s;
  -o-transition: 0.5s;
}
.link-home:hover {
  width: 140px;
  color: #fff;
  background-color: #0989ff;
}

.product_type {
  padding: 30px 40px;
  width: 100%;
}

.product_type .container {
  max-width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
.product_type .container .box {
  padding: 10px 20px;
  margin-left: 10px;
  display: flex;
  flex-direction: column;
  gap: 2rem;
  align-items: center;
}
.product_type .container .box .img_box {
  height: 200px;
  width: 200px;
  border-radius: 50%;
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  -ms-border-radius: 50%;
  -o-border-radius: 50%;
  background: #e8f4ff;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}
.product_type .container .box .img_box img {
  height: 120px;
  width: 120px;
  transition: 0.5s;
  -webkit-transition: 0.5s;
  -moz-transition: 0.5s;
  -ms-transition: 0.5s;
  -o-transition: 0.5s;
}

.product_type .container .box .img_box img:hover {
  transform: scale(1.1);
  -webkit-transform: scale(1.1);
  -moz-transform: scale(1.1);
  -ms-transform: scale(1.1);
  -o-transform: scale(1.1);
}
.product_type .container .box p {
  color: #9a9a9a;
  font-size: 1.15rem;
}

.about {
  padding: 40px 50px;
  width: 100%;
}

.about .container {
  max-width: 100%;
  display: flex;
  justify-content: space-between;
}

.about .container .box {
  padding: 20px 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  justify-content: space-between;
  background: #ecf2f7;
  width: 270px;
  height: 100px;
  border-radius: 5px;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  -ms-border-radius: 5px;
  -o-border-radius: 5px;
  cursor: pointer;
}
.about .container .box .icon {
  color: #e44c8c;
  font-size: 32px;
}

.about .container .box .detail h3 {
  color: #010f1c;
}
.about .container .box .detail p {
  color: #9a9a9a;
}

.product {
  padding: 30px 40px;
  width: 100%;
}
.product h2 {
  font-size: 32px;
  color: #010f1c;
  margin-bottom: 20px;
}

.product .container {
  display: flex;
  gap: 8px;
  /* justify-content: space-between; */
  flex-wrap: wrap;
}

.product .container .box {
  padding: 10px 20px;
  overflow: hidden;
  width: 280px;
  height: 380px;
  border: 1px solid #e2e0e0;
  margin-bottom: 20px;
  transition: 0.5s;
  /*display: flex;
  flex-direction: column;
  justify-content: space-between;
  */
  -webkit-transition: 0.5s;
  -moz-transition: 0.5s;
  -ms-transition: 0.5s;
  -o-transition: 0.5s;
  cursor: pointer;
  border-radius: 5px;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  -ms-border-radius: 5px;
  -o-border-radius: 5px;
}

.product .container .box:hover {
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
}

.product .container .box .img_box {
  display: flex;
  justify-content: space-between;
  border-bottom: 1px solid #e2e0e0;
}

.product .container .box .img_box img {
  width: 250px;
  height: 250px;
}
.product .container .box .img_box .icon {
  position: relative;
  z-index: 1;
  right: -70px;
  display: flex;
  flex-direction: column;
  margin-top: 20px;
  transition: 0.5s;
  -webkit-transition: 0.5s;
  -moz-transition: 0.5s;
  -ms-transition: 0.5s;
  -o-transition: 0.5s;
}
.product .container .box .img_box:hover .icon {
  transform: translateX(-60px);
  -webkit-transform: translateX(-60px);
  -moz-transform: translateX(-60px);
  -ms-transform: translateX(-60px);
  -o-transform: translateX(-60px);
}

.product .container .box .img_box .icon li {
  list-style: none;
  padding: 10px 10px;
  color: #010f1c;
  cursor: pointer;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  background: #fff;
  transition: 0.5s;
  -webkit-transition: 0.5s;
  -moz-transition: 0.5s;
  -ms-transition: 0.5s;
  -o-transition: 0.5s;
  font-size: 18px;
}

.product .container .box .img_box .icon li:hover {
  background: #0989ff;
  color: #fff;
}
.product .container .box .detail {
  margin-top: 10px;
}
.product .container .box .detail p {
  color: #9a9a9a;
  font-size: 14px;
}
.product .container .box .detail h3 {
  font-size: 16px;
  margin-top: 5px;
  color: #010f1c;
  transition: 0.5s;
  -webkit-transition: 0.5s;
  -moz-transition: 0.5s;
  -ms-transition: 0.5s;
  -o-transition: 0.5s;
}
.product .container .box:hover .detail h3 {
  color: #0989ff;
}

.product .container .box .detail h4 {
  margin-top: 5px;
  font-size: 14px;
  color: #0989ff;
  letter-spacing: 1px;
}
.banner {
  padding: 20px 30px;
  width: 100%;
}

.banner .container {
  padding: 30px 40px;
  background: #0989ff;
  border-radius: 10px;
  -webkit-border-radius: 10px;
  -moz-border-radius: 10px;
  -ms-border-radius: 10px;
  -o-border-radius: 10px;
  display: flex;
  justify-content: space-between;
}

.banner .container .detail {
  margin-top: 10%;
  margin-left: 20px;
}

.banner .container .detail h4 {
  color: #e3edf6;
  font-size: 16px;
}
.banner .container .detail h3 {
  margin-top: 10px;
  font-size: 42px;
  margin-right: 20%;
  letter-spacing: 1px;
  color: #fff;
}
.banner .container .detail p {
  margin-top: 30px;
  color: #fff;
  font-size: 32px;
  letter-spacing: 1.5px;
  font-weight: 600;
  margin-bottom: 30px;
}

.banner .container .detail .link {
  display: flex;
  justify-content: space-between;
  width: 160px;
  padding: 10px 30px;
  color: #e8f4ff;
  background: #010f1c;
  text-decoration: none;
  border-radius: 5px;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  -ms-border-radius: 5px;
  -o-border-radius: 5px;
  transition: 0.5s;
  -webkit-transition: 0.5s;
  -moz-transition: 0.5s;
  -ms-transition: 0.5s;
  -o-transition: 0.5s;
}
.banner .container .detail .link:hover {
  width: 165px;
  color: #010f1c;
  background: #e8f4ff;
}
